.usluga {
    padding: 100px 0;
    background: #2f343a;

    &__title {
        font-weight: bold;
        font-size: 40px;
        line-height: 49px;
        text-transform: uppercase;
        color: var(--white);
        padding: 20px 0 30px;

        @media (max-width: 900px) {
            font-size: 30px;
            padding: 10px 10px;
        }
    }

    &__info{

      &:before {
            content: "";
            width: 90px;
            height: 6px;
            background: var(--gold);
            display: block;
        }
    }

    &__blocks{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    &__block {
        width: 276px;
        height: 180px;
        color: var(--white);
        text-align: center;
        background: #393f47;
        margin: 1px 1px;
        cursor: pointer;
        transition: all .3s;

        &:hover {
            background: var(--gold);
        }

        &:hover i{
            background: var(--white);
            color: var(--dark);
        }
    }

    

    &__block-active {
        background: var(--gold);
    }

    &__icon {
        width: 70px;
        height: 70px;
        border-radius: 50%;
        color: var(--white);
        font-size: 32px;
        line-height: 77px;
        margin: 30px 0 30px;
    }

    &__icon-action {
        background: var(--white);
        color: var(--dark);
    }

    &__name {
        font-weight: bold;
        font-size: 18px;
        line-height: 22px;
        text-align: center;
        letter-spacing: .05em;
        text-transform: uppercase;
    }
}