.topline {
    

    &__bg {
        background: var(--gold);
    }

    &__row {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        padding: 10px 0;
    }
    
    &__row a{
        color: var(--white);
        text-decoration-line: none;
        font-size: 12px;
        line-height: 14px;
    }

    &__link {
        opacity: .5;
        transition: all .3s;

        &-active {
            opacity: 1;
        }

        &:hover {
            opacity: 1;
        }
    }
}

.menu {
    background: var(--dark);

    &__block {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 20px 20px;
        position: relative;
    }

    &__logo {
        display: flex;
        color: var(--white);
        align-items: center;
    }

    &__img {
        border-right: 1px solid #f2f2f2;
        padding-right: 30px;

        @media (max-width: 480px) {
            border-right: none;
            padding-right: 0;
        }
    }

    &__location{
        padding-left: 65px;
        line-height: 56px;
        letter-spacing: .05em;

        @media (max-width: 480px) {
            display: none;
        }

        &-maker{
            font-size: 35px;
            color: var(--gold);
            position: relative;
            left: -13px;
            top: 7px;
        }
    }

    &__list a {
        color: var(--white);
        font-size: 14px;
        line-height: 16px;
        text-transform: uppercase;
        text-decoration-line: none;
        transition: all .5s;

        &:hover {
            color: var(--gold);
        }
    }

    &__list {
        letter-spacing: .05em;

        @media (max-width: 880px) {
            display: none;
        }

        &-burger {
            font-size: 25px;
            color: var(--gold);
            cursor: pointer;
            display: none;

            @media (max-width: 880px) {
                display: block;
            }
        }
    }

    &__list-mobile {
        background: var(--gold);
        text-align: center;
        transition: all .5s;
        height: 0;
        
        a {
            color: var(--white);
            font-size: 14px;
            line-height: 16px;
            text-transform: uppercase;
            text-decoration-line: none;
            transition: all .5s;
            display: block;
            padding: 20px 0;
    
            &:hover {
                color: var(--dark);
            }
        }
    }

    &__list-mobile-active {
        height: 225px;
    }

    &__list-active a{
        display: block;
        padding: 20px 0;
    }

    &__item:not(:last-child){
        padding-right: 20px;
    }
}

.image {
    background: url("../img/Rectangle.png");
    background-position: center;
    height: 600px;
    position: relative;
    text-align: center;

    @media (max-width: 480px) {
        height: 350px;
    }

    &__block{
        max-width: 900px;
        height: 245px;
        width: 80%;
        border: 1px solid var(--gold);
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
    }

    &__title{
        color: var(--white);
        font-weight: bold;
        font-size: 64px;
        text-transform: uppercase;
        margin: 20px 0;

        @media (max-width: 1000px) {
            font-size: 50px;
            
        }

        @media (max-width: 750px) {
            font-size: 30px;
            padding: 0 10px;
        }
    }

    &__disc {
        color: var(--white);
        font-size: 18px;
        line-height: 28px;
        padding: 0 50px;

        @media (max-width: 1000px) {
            font-size: 16px;
        }

        @media (max-width: 480px) {
            font-size: 14px;
            padding: 0;
        }
    }

    &__mouse {
        position: absolute;
        bottom: 50px;

        @media (max-width: 480px) {
            display: none;
        }
    }
}