.slider {
    position: relative;
    background: #393f47;
    overflow: hidden;

    &__container {
        position: relative;
        z-index: 2;
    }

    &:before {
        content: "";
        width: 370px;
        height: 500px;
        display: block;
        background: #2f343a;
        position: absolute;
        top: 0;
        left: -157px;
        right: 0;
        bottom: 0;
        margin: auto;
        z-index: 1;

        @media (max-width: 845px) {
            left: 20px;
        }

        @media (max-width: 760px) {
            left: 50%;
            height: 1000px;
        }
    }

    &__title {
        font-weight: bold;
        font-size: 40px;
        line-height: 49px;
        text-transform: uppercase;
        color: var(--white);
        padding: 20px 0;

        @media (max-width: 900px) {
            font-size: 30px;
            padding: 0 10px;
        }
    }

    &__row{
        display: flex;
        padding: 100px 0;

        @media (max-width: 760px) {
            flex-wrap: wrap;
        }
    }

    &__info{
        width: 400px;

        @media (max-width: 760px) {
            width: 100%;
            margin-bottom: 40px;
        }

        &:before {
            content: "";
            width: 90px;
            height: 6px;
            background: var(--gold);
            display: block;
        }

    }

    &__disc{
        font-weight: normal;
        font-size: 18px;
        line-height: 28px;
        color: var(--white);
        padding: 20px 0;

        @media (max-width: 900px) {
            font-size: 14px;
            padding: 10px 0px;
        }
    }

    &__btn {
        width: 220px;
        height: 55px;
        background: var(--gold);
        border: none;
        color: var(--white);
        font-weight: 500;
        font-size: 16px;
        line-height: 25px;
        text-align: center;
        letter-spacing: .1em;
        text-transform: uppercase;
        cursor: pointer;

        &:hover {
            box-shadow: 0 0 10px rgba(0,0,0,.5);
        }
    }

    &__buttons {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        color: var(--gold);
        text-transform: uppercase;
        font-weight: 500;
        font-size: 16px;
        line-height: 25px;
        text-align: center;
        letter-spacing: .1em;

        @media (max-width: 760px) {
            width: 100%;
            flex-direction: row-reverse;
            justify-content: space-around;
        }
    }

    &__next:after {
        content: url("../img/next.png");
        color: var(--gold);
        position: relative;
        top: 8px;
        right: -25px;

        @media (max-width: 555px) {
            display: none;
        }
    }
    
    &__next:hover {
        color: var(--white);
        cursor: pointer;
        transition: all .3s;
    }

    &__prev{
        width: 170px;
    }

    &__prev:before {
        content: url("../img/prev.png");
        color: var(--gold);
        position: relative;
        top: 8px;
        right: -25px;
        margin-right: 40px;

        @media (max-width: 555px) {
            display: none;
        }
    }
    
    &__prev:hover {
        color: var(--white);
        cursor: pointer;
        transition: all .3s;
    }

    &__img img {

    }

    &__img {
        width: 445px;
        height: 266px;

        @media (max-width: 760px) {
            margin: 0 auto 20px;
        }

        & img {
            width: 100%;
            height: auto;
        }

    }

    &__img-photo {
        display: none;
    }

    &__img-photo-active {
        display: block;
    }
}