.footer {
    background: var(--gold);

    &__logo {

        @media(max-width: 650px){
            width: 100%;
            margin-bottom: 15px;
        }
    }

    &__items {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        padding: 30px 0;
    }

    &__adress {
        font-weight: normal;
        font-size: 14px;
        line-height: 16px;
        color: var(--white);
        margin-bottom: 10px;
        padding: 0 10px;
    }

    &__email {
        @media(max-width: 480px) {
            margin: 15px 0;
        }
    }

    &__email a{
        color: var(--black);
        text-decoration-line: none;
        padding: 0 10px;
    }

    &__telefon a {
        display: block;
        color: var(--white);
        text-decoration-line: none;
        font-weight: normal;
        font-size: 14px;
        line-height: 16px;
        padding: 0 10px;
    }

    &__telefon a:first-child {
        margin-bottom: 10px;
    }

    &__razrab {
        align-self: flex-end;
        width: 360px;
        text-align: right;
        padding: 0 10px;

        @media (max-width: 1055px) {
            width: 215px;
        }

        @media (max-width: 888px) {
            width: 100%;
            padding: 10px 0;
            text-align: center;
            margin-top: 10px;
        }
    }

    &__razrab a {
        color: var(--black);
        font-weight: normal;
        font-size: 14px;
        line-height: 16px;
        text-align: right;
        text-decoration-line: underline;
    }

}