.company {
    padding: 100px 0;
    background: #2f343a;

    &__title {
        font-weight: bold;
        font-size: 40px;
        line-height: 49px;
        text-transform: uppercase;
        color: var(--white);
        padding: 20px 0 30px;

        @media (max-width: 900px) {
            font-size: 30px;
            padding: 10px 10px;
        }
    }

    &__info{
        display: flex;
        position: relative;

        &:before {
            content: "";
            width: 90px;
            height: 6px;
            background: var(--gold);
            display: block;
            position: absolute;
        }

        @media (max-width: 980px) {
            flex-wrap: wrap;
        }

        &:after {
            content: "";
            width: 370px;
            height: 450px;
            background: #393f47;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            margin: auto;
            z-index: 1;

            @media (max-width: 690px) {
                display: none;
            }
        }
    }

    &__disc {
        width: 445px;
        color: var(--white);
        font-weight: normal;
        font-size: 16px;
        line-height: 23px;    

        @media (max-width: 980px) {
            width: 100%;
            margin-bottom: 20px;
        }
    }

    &__cards{
        display: flex;
        flex-wrap: wrap;

        @media (max-width: 980px) {
            justify-content: center;
        }
    }

    &__card {
        width: 200px;
        height: 160px;
        background: var(--white);
        border: 1px solid var(--gold);
        text-align: center;

        @media (max-width: 420px) {
            width: 90%;
        }
    }

    &__number{
        font-weight: bold;
        font-size: 64px;
        line-height: 75px;
        text-align: center;
        letter-spacing: .05em;
        text-transform: uppercase;
        color: var(--dark);
        padding-top: 20px;
    }

    &__mest {
        font-weight: normal;
        font-size: 18px;
        line-height: 21px;
        text-align: center;
        letter-spacing: .05em;
        padding-bottom: 8px;
    }

    &__location{
        font-weight: normal;
        font-size: 12px;
        line-height: 14px;
        text-align: center;
    }

    &__card-img {
        background: url("../img/company_img.png");
        background-size: cover;
        background-position: center;

    }

    &__item {
        z-index: 2;
    }

    &__item:first-child {
        margin-right: 85px;

        @media (max-width: 980px) {
            margin-right: 0;
        }
    }
}