
@import "../css/header.css";
@import "../css/slider.css";
@import "../css/uslugi.css";
@import "../css/company.css";
@import "../css/footer.css";

:root {
    --default: "Roboto" , sans-serif;
    --gray: #828282;
    --blue: #56ccf2;
    --yellow: #f2c94c;
    --white: #fff;
    --black: #000;
    --gold: #c8a35f;
    --dark: #2f343a;
}

*, ::after, ::before {
    box-sizing: border-box;
}

body, html {
    margin: 0;
    padding: 0;
    font-family: var(--default);
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
    color: var(--fonts-gray);
    background-color: var(--white);
}

.container {
    max-width: 1170px;
    margin: 0 auto;

    @media (max-width: 1170px) {
        width: 95%;
    }
}

.maps {
    border: 0;
    width: 100%;
    height: 450px;
    margin: 0;
    padding: 0;
    margin-bottom: -8px;
}

.arrowup {
    position: fixed;
    right: 5%;
    bottom: 10%;
    border-radius: 50%;
    width: 48px;
    height: 48px;
    background: #f2c94c;
    background: var(--gold);
    color: #f7f7f7;
    color: var(--white);
    font-size: 1.5rem;
    z-index: 3;
    cursor: pointer;
    text-align: center;
    line-height: 48px;
}